/* Contenedor principal del dado */
.dice-container {
    text-align: center;
    margin-top: 20px;
  }
  
  /* Botón con estilo y animación */
  .dice-button {
    background-color: #8B4513; /* Color de fondo marrón oscuro */
    color: #F4A460; /* Color del texto */
    border: none;
    border-radius: 10px;
    padding: 10px 20px;
    font-family: 'Uncial Antiqua', serif; /* Fuente medieval */
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
  }
  
  .dice-button:hover {
    background-color: #DAA520; /* Color al pasar el ratón */
    transform: scale(1.1); /* Escala al pasar el ratón */
  }
  
  /* Imagen del dado con animación de rodar */
  .dice-image {
    margin-top: 0%;
    display: inline-block;
    width: 80px; /* Ajusta el tamaño según sea necesario */
    height: 80px; /* Ajusta el tamaño según sea necesario */
  }
  
  .dice-face {
    width: 100%;
    height: 100%;
    object-fit: cover;
    animation: roll-animation 1s ; /* Animación de rodar */
    transition: transform 3s ease; /* Duración de la animación */
  }
  
  /* Animación de rodar */
  @keyframes roll-animation {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(720deg);
    }
  }
  

  /* Texto del resultado del dado */
.dice-result-text {
  font-size: 50px;
  color: #ffffff; /* Color del texto */
  font-family: 'Comic Sans MS', 'Comic Sans', cursive; /* Fuente tipo comic */
  text-shadow: #000000 4px 4px 4px; /* Sombra del texto */
  margin-top: 20px;
}
